.burger {
    display: none;
    position: fixed;
    top: 0px;
    right: 10px;
    left: 10px;
    z-index: 1;
    background: var(--comp-bg-3);
    border-radius: 5px;
    box-shadow: rgb(0 0 0 / 24%) 0px 3px 8px;
    transition: background-color 300ms linear;
}
.burger__area {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    background-color: rgba(0, 0, 0, 0.548);
    z-index: 1;
}
.burger-icon {
    color: var(--text-primary);
    margin: 10px 20px;
    width: 25px;
    height: 25px;
    cursor: pointer;
}
.burger-icon:hover {
    color: var(--accent);
}
.sidebar-wrapper h1.burger__name {
    margin: 0px;
    margin-left: 15px;
    align-self: center;
}
.burger__toggle {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 2;
    margin-right: 10px;
}

.sidebar {
    position: fixed;
    background: var(--comp-bg-1);
    transition: background-color 300ms linear;
    border-radius: 5px;
    height: 96%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.sidebar__header {
    width: 300px;
    height: 320px;
    background: var(--comp-bg-2);
    transition: background-color 300ms linear, border-bottom 300ms linear;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid var(--comp-border);
}

.sidebar__contacts {
    display: flex;
    gap: 10px;
    margin-top: 30px;
}

.sidebar__ava {
    width: 140px;
    height: 140px;
    border: 1px solid var(--accent);
    border-radius: 102px;
    margin-top: 25px;
    transition: border 300ms linear;
}

.sidebar-wrapper h1 {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.05em;
    color: var(--text-primary);
    margin-top: 20px;
    transition: color 300ms linear;
}

.sidebar__header h2 {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: var(--text-secondary);
    margin-top: 10px;
    transition: color 300ms linear;
}

.sidebar__nav {
    margin-top: 40px;
}

.sidebar__nav ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    list-style-type: none;
}

.sidebar__nav a {
    /* color: var(--text-primary); */
    text-decoration: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.05em;
    padding-top: 10px;
    padding-bottom: 10px;

    background-image: linear-gradient(
        to right,
        var(--accent),
        var(--accent) 50%,
        var(--text-primary) 50%
    );
    background-size: 200% 100%;
    background-position: -100%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: all 0.3s ease-in-out;
}

.sidebar__nav a:before {
    content: '';
    background: var(--accent);
    display: block;
    position: relative;
    top: 22px;
    left: 0;
    width: 0;
    height: 3px;
    transition: all 0.3s ease-in-out;
}

.sidebar__nav a:hover {
    /* color: var(--accent); */
    background-position: 0;
}

.sidebar__nav a:hover::before {
    width: 100%;
}

.sidebar__footer-wrapper {
    position: fixed;
    width: 300px;
    top: 86%;
}

.sidebar__footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.sidebar__download a {
    display: flex;
    gap: 20px;
    color: var(--text-primary);
    text-decoration: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.05em;
    transition: all 0.3s ease-in-out;
}

.sidebar__download a:hover {
    color: var(--accent);
}

.sidebar__download span {
    align-self: flex-end;
}

.sidebar__download-icon {
    width: 30px;
    height: 30px;
}

@media (max-height: 800px) {
    .sidebar__nav {
        margin-top: 20px;
    }
    .sidebar__nav ul {
        gap: 10px
    }
}

@media (max-width: 1120px) {
    .sidebar {
        height: 97%;
    }
    .sidebar__header {
        width: 220px;
        height: 280px;
    }
    .sidebar__header h1 {
        font-size: 15px;
    }
    .sidebar__header h2 {
        font-size: 13px;
    }
    .sidebar__nav a {
        font-size: 15px;
    }
    .sidebar__footer-wrapper {
        width: 220px;
    }
    .sidebar__footer-toggle span{
        font-size: 13px;
    }
    .sidebar__download a {
        font-size: 15px;
        gap: 10px;
    }
    .sidebar__download-icon {
        width: 22px;
        height: 22px;
    }
    .sidebar__contacts{
        margin-top: 25px;
        gap: 8px;
    }
    .sidebar__contacts a svg {
        width: 22px;
        height: 22px;
    }
    .sidebar__ava {
        width: 120px;
        height: 120px;
        margin-top: 15px;
    }
}
@media (max-width: 840px) {
    .sidebar {
        height: 98%;
    }
}
@media (max-width: 760px) {
    .sidebar {
        /* display: none; */
        margin-left: -220px;
        z-index: 2;
        top: 0px;
        left: 0px;
        height: 100%;
        transition: margin-left 0.4s linear;
        background: var(--comp-bg-3);
    }
    .burger {
        display: flex;
    }
}
@media (max-width: 475px) {
    .burger .custom-toggle span {
        display: none;
    }
}
@media (max-width: 390px) {
    .sidebar-wrapper h1.burger__name {
        margin-left: 0px;
        font-size: 15px;
    }
}